<template lang="html">
  <el-form ref="form" :model="form" rel="form"  label-width="90px"  style="margin:20px;width:60%;min-width:600px;">
    <el-form-item label="接口名称" prop="interfaceName":rules="{required: true, message: '接口名称不能为空', trigger: 'blur'}">
			<el-input v-model="form.interfaceName" placeholder="接口名称">
      </el-input>
		</el-form-item>
    <el-form-item label="接口URL" prop="interfaceUrl":rules="{required: true, message: '接口URL不能为空', trigger: 'blur'}">
      <el-input v-model="form.interfaceUrl" placeholder="接口URL">
        <template slot="prepend">http://</template>
      </el-input>
    </el-form-item>
    <el-form-item label="请求方式" prop="interfaceType" :rules="{required:true,message:'请求方式',trigger:'change'}">
			<el-select v-model="form.interfaceType" placeholder="请求方式" >
				<el-option label="POST" value="POST"></el-option>
				<el-option label="GET" value="GET"></el-option>
			</el-select>
		</el-form-item>
    <el-form-item label="接口参数" prop="interfaceParam">
      <el-input v-model="form.interfaceParam" placeholder="接口参数，多个参数[,]分割">
      </el-input>
    </el-form-item>
    <el-form-item label="活动形式" prop="interfaceDesc">
      <el-input type="textarea" v-model="form.interfaceDesc"></el-input>
    </el-form-item>
 <el-form-item>
   <el-button type="primary" @click="submitForm"  v-loading='loading'>录入</el-button>
   <el-button @click="resetForm">重置</el-button>
 </el-form-item>
  </el-form>
</template>

<script>

import * as API from '../api/api'

export default {
  data(){
    return {
      form:{
        interfaceName:"",
        interfaceUrl:"",
        interfaceType:"",
        interfaceParam:"",
        interfaceDesc:""
      },
      loading:false
    }
  },
  methods: {
    submitForm() {
      this.$refs.form.validate((valid)=>{
        if(valid){
          this.loading = true
          API.saveInterface(this.form).then(res=>{
            this.loading = false
            if(res.status==200){
              this.$message({
                 message: res.message,
                 type: 'success'
              })
              this.$refs.form.resetFields()
            }else{
              this.$message.error(res.message);
            }
          })
        }else{
            this.loading = false
          console.log('error submit');
          return false;
        }
      })
    },
    resetForm() {
      this.$refs.form.resetFields();
    }
  }


}
</script>

<style lang="css">
</style>



